.card-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition:
    z-index 0.3s step-end,
    top 0ms ease-in-out 300ms;
  z-index: -1000;
}

.card-modal .overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  border-radius: var(--box-border-radius);
  opacity: 0;
  transition: opacity 100ms ease-in-out 200ms;
}

.card-modal .card-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1.15);
  width: 95%;
  max-width: 700px;
  background: #fff;
  padding: 25px;
  opacity: 0;
  border-radius: 20px;
  box-shadow: 0 2px 2px 5px rgba(0, 0, 0, 0.05);
  transition: all 300ms ease-in-out;
  height: 90%;
  overflow: auto;
}

.card-modal .card-modal-content h2 {
  margin: 10px 0;
  font-size: 25px;
  color: var(--green);
  text-align: center;
}

.card-modal .card-modal-content p {
  margin: 15px 0;
  font-size: 16px;
  color: var(--darkblue);
}

.card-modal.active {
  top: 0;
  transition: top 0ms ease-in-out 0ms;
  z-index: 99999;
}
.card-modal.active .overlay {
  opacity: 1;
  transition: all 300ms ease-in-out;
}
.card-modal.active .card-modal-content {
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
